import { AgmMap, MapsAPILoader } from "@agm/core";
import {
  Component,
  OnInit,
  ViewChild,
  ViewEncapsulation,
  AfterViewInit
} from "@angular/core";

import { Subscription, Observable, of } from "rxjs";
import { ToastrService } from "ngx-toastr";
import { GoogleLocation } from "@app/shared/model/google-location";
import { PlayingFieldDto } from "@app/shared/model/google-map";
import { GoogleService } from "@app/shared/service/google.service";
import { GeoLocationService } from "@app/shared/service/geo-location.service";
import { AuthService } from "@app/core/service";
import { LocationService } from "@app/shared/service/location.service";
import { FormControl } from "@angular/forms";
import {
  delay,
  startWith,
  switchMap,
  map,
  catchError,
  tap
} from "rxjs/operators";
import { MatAutocompleteSelectedEvent } from "@angular/material/autocomplete";
import { MatDialog } from "@angular/material/dialog";
import { UserService } from "@app/shared/service/user.service";
import { UserDto } from "@app/shared/model";
import { AssignPlayingFieldAndRegisterDialogComponent } from "../assign-playing-field-and-register-dialog/assign-playing-field-and-register-dialog.component";

@Component({
  selector: "app-main-page",
  templateUrl: "./main-page.component.html",
  styleUrls: ["./main-page.component.scss"],
  encapsulation: ViewEncapsulation.None
})
export class MainPageComponent implements OnInit, AfterViewInit {
  lastSelectedInfoWindow: any;
  selectedLocationControl = new FormControl();
  locations: Observable<any[]>;
  searchedLocations: any[];
  userLocation: GoogleLocation = {
    lat: 0,
    lng: 0,
    zoom: 13
  };

  userLocationClone: GoogleLocation;
  selectedLocation: GoogleLocation;
  selectedLocation2: GoogleLocation = {
    lat: 50.0,
    lng: 20.0,
    zoom: 13
  };

  customerLabel = "Tu jesteś";
  customerMapIcon = "assets/icons/customer-map-marker.svg";
  shopMapIcon = "assets/icons/shop-map-marker.svg";
  registeredIcon = "assets/icons/registered-map-marker.svg";
  ownerPfIcon = "assets/icons/owner-pf-marker.svg";
  subscription: Subscription;
  public geoCoder: any;
  playingFieldList: PlayingFieldDto[] = [];
  isRegistered: boolean;

  loggedUser: UserDto;
  @ViewChild(AgmMap, { static: false }) map: AgmMap;

  ngAfterViewInit(): void {
    this.getLoggedUser();
  }

  constructor(
    private googleService: GoogleService,
    private geoLocationService: GeoLocationService,
    private authService: AuthService,
    private toastr: ToastrService,
    private mapsApiLoader: MapsAPILoader,
    private locationService: LocationService,
    private userService: UserService,
    private dialog: MatDialog
  ) {
    this.mapsApiLoader.load().then(() => {
      this.geoCoder = new google.maps.Geocoder();
    });
  }

  ngOnInit() {
    this.subscription = this.subscribeUserSelectedLocation();
    this.locations = this.handleSelectedLocationChange();
    this.selectedLocation = this.userLocation;

    if (navigator) {
      navigator.geolocation.getCurrentPosition(pos => {
        this.userLocation.lng = pos.coords.longitude;
        this.userLocation.lat = pos.coords.latitude;
        this.userLocationClone = JSON.parse(JSON.stringify(this.userLocation));
        this.findPlaces(pos.coords.latitude, pos.coords.longitude);
      });
    }
  }

  markerClick(infoWindow: any) {
    if (infoWindow == this.lastSelectedInfoWindow) {
      return;
    }
    if (this.lastSelectedInfoWindow != null) {
      try {
        this.lastSelectedInfoWindow.close();
      } catch {} //in case if you reload your markers
    }
    this.lastSelectedInfoWindow = infoWindow;
  }

  mapReady(map) {
    map.addListener("dragend", () => {
      this.findPlaces(this.selectedLocation2.lat, this.selectedLocation2.lng);
    });
  }

  centerChange(e) {
    this.selectedLocation2.lat = e.lat;
    this.selectedLocation2.lng = e.lng;
  }

  private subscribeUserSelectedLocation() {
    return this.locationService.selectedLocation.subscribe(
      this.setSelectedLocation
    );
  }

  private setSelectedLocation = (location: GoogleLocation) => {
    if (location) {
      this.selectedLocation2 = {
        lat: location.lat,
        lng: location.lng,
        zoom: 13
      };
      this.findPlaces(this.selectedLocation2.lat, this.selectedLocation2.lng);
    }
  };

  private findPlaces(lat: number, lng: number) {
    this.googleService
      .getGooglePlaces(lat, lng)
      .subscribe(this.handleSuccessResponse);
  }

  private handleSuccessResponse = (response: PlayingFieldDto[]) => {
    console.log(response);
    this.playingFieldList = response;
    if (!this.playingFieldList.length) {
      this.toastr.info("Nie znaleziono żadnych orlików w tym miejscu!");
    }
  };

  private handleSelectedLocationChange = (): Observable<any[]> => {
    return this.selectedLocationControl.valueChanges.pipe(
      switchMap((address: string) => {
        return this.geoLocationService.getPositions(address);
      }),
      map((data: any) => {
        return data && data;
      }),
      catchError((error: any) => {
        return of([]);
      }),
      tap((data: any[]) => {
        this.searchedLocations = data;
      })
    );
  };

  setLocation($event: MatAutocompleteSelectedEvent) {
    const location: string = $event.option.value;
    const index = this.searchedLocations.findIndex(
      elem => elem.formatted_address === location
    );

    if (index !== -1) {
      const loc: google.maps.GeocoderResult = this.searchedLocations[index];
      const selectedLocation: GoogleLocation = {
        lat: loc.geometry.location.lat(),
        lng: loc.geometry.location.lng(),
        zoom: 13
      };

      this.locationService.setSelectedLocation(selectedLocation);
      this.userLocation = selectedLocation;
      //this.selectedLocation = selectedLocation;
    }
  }

  getIcon(pf: PlayingFieldDto) {
    if (pf.userDto && this.loggedUser) {
      if (pf.userDto.id === this.loggedUser.id) {
        return this.ownerPfIcon;
      }
    }
    if (pf.registered === true && pf.active) {
      return this.registeredIcon;
    } else {
      return this.shopMapIcon;
    }
  }

  getMarkerLabel(name: string) {
    return {
      color: "red",
      text: name
    };
  }

  assignPFAndRegisterDialog(pf: PlayingFieldDto) {
    this.dialog.open(AssignPlayingFieldAndRegisterDialogComponent, {
      width: "350px",
      data: { playingField: pf }
    });
  }

  isLogged(): boolean {
    return this.authService.isLogged();
  }

  getLoggedUser() {
    if (this.authService.isLogged()) {
      this.userService.getLoggedUser().subscribe(response => {
        this.loggedUser = response;
      });
    }
  }
  resetLocation() {
    this.userLocation = this.userLocationClone;
    this.findPlaces(this.userLocation.lat, this.userLocation.lng);
  }
  isPFOwner(pf: PlayingFieldDto): boolean {
    if (pf.registered) {
      if (!pf.userDto || !pf.userDto.id || !this.loggedUser) {
        return false;
      }
      return pf.userDto.id === this.loggedUser.id;
    } else return false;
  }
}
